// panel.requiredIf: "Use the magic wand icon to set a conditional rule that prevents survey submission unless at least one nested question has an answer." => "Uyj ikony magicznej rdki, aby ustawi regu warunkow, ktra uniemoliwia przesanie ankiety, chyba e co najmniej jedno zagniedone pytanie ma odpowied."
// panel.questionTitleLocation: "Applies to all questions within this panel. If you want to override this setting, define title alignment rules for individual questions. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "Dotyczy wszystkich pyta w tym panelu. Jeli chcesz zastpi to ustawienie, zdefiniuj reguy wyrwnania tytuw dla poszczeglnych pyta. Opcja \"Dziedzicz\" stosuje ustawienie na poziomie strony (jeli jest ustawione) lub na poziomie ankiety (domylnie \"Gra\")."
// panel.questionErrorLocation: "Sets the location of an error message in relation to all questions within the panel. The \"Inherit\" option applies the page-level (if set) or survey-level setting." => "Ustawia lokalizacj komunikatu o bdzie w odniesieniu do wszystkich pyta w panelu. Opcja \"Dziedzicz\" stosuje ustawienie na poziomie strony (jeli jest ustawione) lub na poziomie ankiety."
// panel.questionsOrder: "Keeps the original order of questions or randomizes them. The \"Inherit\" option applies the page-level (if set) or survey-level setting." => "Zachowuje pierwotn kolejno pyta lub losuje je. Opcja \"Dziedzicz\" stosuje ustawienie na poziomie strony (jeli jest ustawione) lub na poziomie ankiety."
// panel.page: "Repositions the panel to the end of a selected page." => "Przesuwa panel na koniec zaznaczonej strony."
// panel.innerIndent: "Adds space or margin between the panel content and the left border of the panel box." => "Dodaje odstp lub margines midzy zawartoci panelu a lew krawdzi ramki panelu."
// panel.startWithNewLine: "Unselect to display the panel in one line with the previous question or panel. The setting doesn't apply if the panel is the first element in your form." => "Usu zaznaczenie, aby wywietli panel w jednym wierszu z poprzednim pytaniem lub panelem. To ustawienie nie ma zastosowania, jeli panel jest pierwszym elementem formularza."
// panel.state: "Choose from: \"Expanded\" - the panel is displayed in full and can be collapsed; \"Collapsed\" - the panel displays only the title and description and can be expanded; \"Locked\" - the panel is displayed in full and cannot be collapsed." => "Do wyboru: \"Rozwinity\" - panel wywietla si w caoci i mona go zwin; \"Zwinity\" - panel wywietla tylko tytu i opis i mona go rozwin; \"Zablokowany\" - panel jest wywietlany w caoci i nie mona go zwin."
// panel.width: "Sets the width of the panel in proportion to other survey elements in the same line. Accepts CSS values (px, %, in, pt, etc.)." => "Ustawia szeroko panelu proporcjonalnie do innych elementw pomiarowych w tej samej linii. Akceptuje wartoci CSS (px, %, in, pt itp.)."
// paneldynamic.name: "A panel ID that is not visible to respondents." => "Identyfikator panelu, ktry nie jest widoczny dla respondentw."
