// paneldynamic.requiredIf: "Use the magic wand icon to set a conditional rule that prevents survey submission unless at least one nested question has an answer." => "Uyj ikony magicznej rdki, aby ustawi regu warunkow, ktra uniemoliwia przesanie ankiety, chyba e co najmniej jedno zagniedone pytanie ma odpowied."
// paneldynamic.templateTitleLocation: "Applies to all questions within this panel. If you want to override this setting, define title alignment rules for individual questions. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "Dotyczy wszystkich pyta w tym panelu. Jeli chcesz zastpi to ustawienie, zdefiniuj reguy wyrwnania tytuw dla poszczeglnych pyta. Opcja \"Dziedzicz\" stosuje ustawienie na poziomie strony (jeli jest ustawione) lub na poziomie ankiety (domylnie \"Gra\")."
// paneldynamic.templateErrorLocation: "Sets the location of an error message in relation to a question with invalid input. Choose between: \"Top\" - an error text is placed at the top of the question box; \"Bottom\" - an error text is placed at the bottom of the question box. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "Ustawia lokalizacj komunikatu o bdzie w odniesieniu do pytania z nieprawidowymi danymi wejciowymi. Wybierz pomidzy: \"Gra\" - tekst bdu jest umieszczany w grnej czci pola pytania; \"Na dole\"  tekst bdu jest umieszczany u dou pola pytania. Opcja \"Dziedzicz\" stosuje ustawienie na poziomie strony (jeli jest ustawione) lub na poziomie ankiety (domylnie \"Gra\")."
// paneldynamic.errorLocation: "Sets the location of an error message in relation to all questions within the panel. The \"Inherit\" option applies the page-level (if set) or survey-level setting." => "Ustawia lokalizacj komunikatu o bdzie w odniesieniu do wszystkich pyta w panelu. Opcja \"Dziedzicz\" stosuje ustawienie na poziomie strony (jeli jest ustawione) lub na poziomie ankiety."
// paneldynamic.page: "Repositions the panel to the end of a selected page." => "Przesuwa panel na koniec zaznaczonej strony."
// paneldynamic.innerIndent: "Adds space or margin between the panel content and the left border of the panel box." => "Dodaje odstp lub margines midzy zawartoci panelu a lew krawdzi ramki panelu."
// paneldynamic.startWithNewLine: "Unselect to display the panel in one line with the previous question or panel. The setting doesn't apply if the panel is the first element in your form." => "Usu zaznaczenie, aby wywietli panel w jednym wierszu z poprzednim pytaniem lub panelem. To ustawienie nie ma zastosowania, jeli panel jest pierwszym elementem formularza."
// paneldynamic.state: "Choose from: \"Expanded\" - the panel is displayed in full and can be collapsed; \"Collapsed\" - the panel displays only the title and description and can be expanded; \"Locked\" - the panel is displayed in full and cannot be collapsed." => "Do wyboru: \"Rozwinity\" - panel wywietla si w caoci i mona go zwin; \"Zwinity\" - panel wywietla tylko tytu i opis i mona go rozwin; \"Zablokowany\" - panel jest wywietlany w caoci i nie mona go zwin."
// paneldynamic.width: "Sets the width of the panel in proportion to other survey elements in the same line. Accepts CSS values (px, %, in, pt, etc.)." => "Ustawia szeroko panelu proporcjonalnie do innych elementw pomiarowych w tej samej linii. Akceptuje wartoci CSS (px, %, in, pt itp.)."
// paneldynamic.templateTitle: "Type in a template for dynamic panel titles. Use {panelIndex} for the panel's general position and {visiblePanelIndex} for its order among visible panels. Insert these placeholders into the pattern to add automatic numbering." => "Wpisz szablon dynamicznych tytuw paneli. Uyj {panelIndex} dla oglnego pooenia panelu i {visiblePanelIndex} dla jego kolejnoci wrd widocznych paneli. Wstaw te symbole zastpcze do wzoru, aby doda automatyczn numeracj."
// paneldynamic.templateTabTitle: "Type in a template for tab titles. Use {panelIndex} for a panel's general position and {visiblePanelIndex} for its order among visible panels. Insert these placeholders into the pattern to add automatic numbering." => "Wpisz szablon tytuw kart. Uyj {panelIndex} dla oglnego pooenia panelu i {visiblePanelIndex} dla jego kolejnoci wrd widocznych paneli. Wstaw te symbole zastpcze do wzoru, aby doda automatyczn numeracj."
// paneldynamic.templateVisibleIf: "This setting allows you to control the visibility of individual panels within the dynamic panel. Use the `{panel}` placeholder to reference the current panel in your expression." => "To ustawienie umoliwia sterowanie widocznoci poszczeglnych paneli w panelu dynamicznym. Uyj symbolu zastpczego \"{panel}\", aby odwoa si do biecego panelu w wyraeniu."
// paneldynamic.titleLocation: "This setting is automatically inherited by all questions within this panel. If you want to override this setting, define title alignment rules for individual questions. The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Top\" by default)." => "To ustawienie jest automatycznie dziedziczone przez wszystkie pytania w tym panelu. Jeli chcesz zastpi to ustawienie, zdefiniuj reguy wyrwnania tytuw dla poszczeglnych pyta. Opcja \"Dziedzicz\" stosuje ustawienie na poziomie strony (jeli jest ustawione) lub na poziomie ankiety (domylnie \"Gra\")."
// paneldynamic.descriptionLocation: "The \"Inherit\" option applies the page-level (if set) or survey-level setting (\"Under the panel title\" by default)." => "Opcja \"Dziedzicz\" stosuje ustawienie na poziomie strony (jeli jest ustawione) lub na poziomie ankiety (domylnie \"Pod tytuem panelu\")."
// paneldynamic.newPanelPosition: "Defines the position of a newly added panel. By default, new panels are added to the end. Select \"Next\" to insert a new panel after the current one." => "Okrela pooenie nowo dodanego panelu. Domylnie nowe panele s dodawane na kocu. Wybierz \"Dalej\", aby wstawi nowy panel po biecym."
// paneldynamic.defaultValueFromLastPanel: "Duplicates answers from the last panel and assigns them to the next added dynamic panel." => "Duplikuje odpowiedzi z ostatniego panelu i przypisuje je do nastpnego dodanego panelu dynamicznego."
// paneldynamic.keyName: "Reference a question name to require a user to provide a unique response for this question in each panel." => "Odwoaj si do nazwy pytania, aby wymaga od uytkownika podania unikatowej odpowiedzi na to pytanie w kadym panelu."
// pehelp.defaultValueExpression: "This setting allows you to assign a default answer value based on an expression. The expression can include basic calculations - `{q1_id} + {q2_id}`, Boolean expressions, such as `{age} > 60`, and functions: `iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc. The value determined by this expression serves as the initial default value that can be overridden by a respondent's manual input." => "To ustawienie umoliwia przypisanie domylnej wartoci odpowiedzi na podstawie wyraenia. Wyraenie moe zawiera podstawowe obliczenia - '{q1_id} + {q2_id}', wyraenia logiczne, takie jak '{wiek} > 60' oraz funkcje: 'iif()', 'today()', 'age()', 'min()', 'max()', 'avg()' itp. Warto okrelona przez to wyraenie suy jako pocztkowa warto domylna, ktra moe zosta zastpiona przez rczne wprowadzanie danych przez respondenta."
// pehelp.resetValueIf: "Use the magic wand icon to set a conditional rule that determines when a respondent's input is reset to the value based on the \"Default value expression\" or \"Set value expression\" or to the \"Default answer\" value (if either is set)." => "Uyj ikony magicznej rdki, aby ustawi regu warunkow, ktra okrela, kiedy dane wejciowe respondenta s resetowane do wartoci na podstawie \"Wyraenia wartoci domylnej\" lub \"Wyraenia wartoci domylnej\" lub wartoci \"Odpowied domylna\" (jeli ktrakolwiek z nich jest ustawiona)."
// pehelp.setValueIf: "Use the magic wand icon to set a conditional rule that determines when to run the \"Set value expression\" and dynamically assign the resulting value as a response." => "Uyj ikony magicznej rdki, aby ustawi regu warunkow, ktra okrela, kiedy uruchomi \"Wyraenie wartoci zestawu\" i dynamicznie przypisa wynikow warto jako odpowied."
// pehelp.setValueExpression: "Specify an expression that defines the value to be set when the conditions in the \"Set value if\" rule are met. The expression can include basic calculations - `{q1_id} + {q2_id}`, Boolean expressions, such as `{age} > 60`, and functions: `iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc. The value determined by this expression can be overridden by a respondent's manual input." => "Okrel wyraenie definiujce warto, ktra ma zosta ustawiona, gdy zostan spenione warunki reguy \"Ustaw warto, jeli\". Wyraenie moe zawiera podstawowe obliczenia - '{q1_id} + {q2_id}', wyraenia logiczne, takie jak '{wiek} > 60' oraz funkcje: 'iif()', 'today()', 'age()', 'min()', 'max()', 'avg()' itp. Warto okrelona przez to wyraenie moe zosta zastpiona przez rczne wprowadzenie danych przez respondenta."
// question.name: "A question ID that is not visible to respondents." => "Identyfikator pytania, ktry nie jest widoczny dla respondentw."
// question.description: "Type a question subtitle." => "Wpisz podtytu pytania."
// question.visibleIf: "Use the magic wand icon to set a conditional rule that determines question visibility." => "Uyj ikony magicznej rdki, aby ustawi regu warunkow, ktra okrela widoczno pytania."
// question.enableIf: "Use the magic wand icon to set a conditional rule that disables the read-only mode for the question." => "Uyj ikony magicznej rdki, aby ustawi regu warunkow, ktra wycza tryb tylko do odczytu dla pytania."
// question.requiredIf: "Use the magic wand icon to set a conditional rule that prevents survey advancing or submission unless the question received an answer." => "Uyj ikony magicznej rdki, aby ustawi regu warunkow, ktra uniemoliwia przejcie do przodu lub przesanie ankiety, chyba e na pytanie otrzymano odpowied."
// question.startWithNewLine: "Unselect to display the question in one line with the previous question or panel. The setting doesn't apply if the question is the first element in your form." => "Usu zaznaczenie, aby wywietli pytanie w jednym wierszu z poprzednim pytaniem lub panelem. To ustawienie nie ma zastosowania, jeli pytanie jest pierwszym elementem formularza."
// question.page: "Repositions the question to the end of a selected page." => "Zmienia pooenie pytania na kocu zaznaczonej strony."
// question.state: "Choose from: \"Expanded\" - the question box is displayed in full and can be collapsed; \"Collapsed\" - the question box displays only the title and description and can be expanded; \"Locked\" - the question box is displayed in full and cannot be collapsed." => "Do wyboru: \"Rozwinite\" - pole pytania wywietla si w caoci i mona je zwin; \"Zwinite\" - pole pytania wywietla tylko tytu i opis i mona je rozwin; \"Zablokowane\" - pole pytania jest wywietlane w caoci i nie mona go zwin."
// question.titleLocation: "Overrides title alignment rules defined on a panel, page or survey level. The \"Inherit\" option applies any higher-level settings (if set) or survey-level setting (\"Top\" by default)." => "Zastpuje reguy wyrwnania tytuw zdefiniowane na poziomie panelu, strony lub ankiety. Opcja \"Dziedzicz\" stosuje wszystkie ustawienia wyszego poziomu (jeli s ustawione) lub ustawienia na poziomie ankiety (domylnie \"Gra\")."
// question.descriptionLocation: "The \"Inherit\" option applies the survey-level setting (\"Under the question title\" by default)." => "Opcja \"Dziedzicz\" stosuje ustawienie na poziomie ankiety (domylnie \"Pod tytuem pytania\")."
