// surveyvalidator.expression: "Use the magic wand icon to set a validation rule for the question." => "Uyj ikony magicznej rdki, aby ustawi regu sprawdzania poprawnoci dla pytania."
// question.textUpdateMode: "Choose from: \"On lost focus\" - the value is updated when the input field loses focus; \"While typing\" - the value is updated in real-time, as users are typing. The \"Inherit\" option applies the survey-level setting (\"On lost focus\" by default)." => "Do wyboru: \"W przypadku utraty fokusu\" - warto jest aktualizowana, gdy pole wejciowe traci fokus; \"Podczas pisania\" - warto jest aktualizowana w czasie rzeczywistym, w miar pisania przez uytkownikw. Opcja \"Dziedzicz\" stosuje ustawienie na poziomie ankiety (domylnie \"Po utracie fokusu\")."
