// pehelp.useDisplayValuesInDynamicTexts: "In single- and multiple-selection question types, each choice option has an ID and display value. When selected, this setting shows a display value instead of an ID value in HTML questions and dynamic titles and descriptions of survey elements." => "W pytaniach typu jednokrotnego i wielokrotnego wyboru kada opcja wyboru ma identyfikator i warto wywietlan. Po wybraniu tej opcji w pytaniach HTML oraz dynamicznych tytuach i opisach elementw ankiety wywietlana jest warto wywietlana zamiast wartoci identyfikatora."
// pehelp.clearIfInvisible: "Choose whether or not to clear question values hidden by conditional logic and when to do it. The \"Inherit\" option applies the survey-level setting (\"Upon survey completion\" by default)." => "Wybierz, czy chcesz wyczyci wartoci pyta ukryte przez logik warunkow i kiedy to zrobi. Opcja \"Dziedzicz\" stosuje ustawienie na poziomie ankiety (domylnie \"Po zakoczeniu ankiety\")."
// pehelp.choicesFromQuestionMode: "Choose from: \"All\" - copies all choice options from the selected question; \"Selected\" - dynamically copies only selected choice options; \"Unselected\" - dynamically copies only unselected choice options. The \"None\" and \"Other\" options are copied by default if enabled in the source question." => "Wybierz spord: \"Wszystkie\" - kopiuje wszystkie opcje wyboru z wybranego pytania; \"Wybrane\" - dynamicznie kopiuje tylko wybrane opcje wyboru; \"Niewybrane\" - dynamicznie kopiuje tylko niezaznaczone opcje wyboru. Opcje \"Brak\" i \"Inne\" s domylnie kopiowane, jeli s wczone w pytaniu rdowym."
// pehelp.showOtherItem: "When selected, users can include additional input in a separate comment box." => "Po wybraniu tej opcji uytkownicy mog uwzgldni dodatkowe dane wejciowe w osobnym polu komentarza."
// pehelp.separateSpecialChoices: "Displays each special choice option (\"None\", \"Other\", \"Select All\") on a new line, even when using a multiple-column layout." => "Wywietla kad opcj wyboru specjalnego (\"Brak\", \"Inne\", \"Zaznacz wszystko\") w nowym wierszu, nawet w przypadku korzystania z ukadu wielokolumnowego."
// pehelp.path: "Specify the location within the service dataset where the target array of objects is located. Leave empty if the URL already points to the array." => "Okrel lokalizacj w zestawie danych usugi, w ktrej znajduje si docelowa tablica obiektw. Pozostaw puste, jeli adres URL wskazuje ju tablic."
// pehelp.titleName: "Enter a uniform property name within the array of objects that contains the values you want to display in the choice list." => "Wprowad jednolit nazw waciwoci w tablicy obiektw, ktra zawiera wartoci, ktre maj by wywietlane na licie wyborw."
// pehelp.allowEmptyResponse: "Select to allow the service to return an empty response or array." => "Zaznacz, aby zezwoli usudze na zwrcenie pustej odpowiedzi lub tablicy."
// pehelp.choicesVisibleIf: "Use the magic wand icon to set a conditional rule that determines the visibility of all choice options." => "Uyj ikony magicznej rdki, aby ustawi regu warunkow, ktra okrela widoczno wszystkich opcji wyboru."
// pehelp.rateValues: "The left value serves as an item ID used in conditional rules, the right value is displayed to respondents." => "Lewa warto suy jako identyfikator elementu uywany w reguach warunkowych, prawa warto jest wywietlana respondentom."
// rating.displayMode: "\"Auto\" selects between the \"Buttons\" and \"Dropdown\" modes based on the available width. When the width is insufficient to display buttons, the question displays a dropdown." => "\"Auto\" wybiera midzy trybami \"Przyciski\" i \"Rozwijane\" w zalenoci od dostpnej szerokoci. Gdy szeroko jest niewystarczajca do wywietlenia przyciskw, w pytaniu zostanie wywietlona lista rozwijana."
// pehelp.valuePropertyName: "Allows you to connect questions that produce results in different formats. When such questions are linked together using a join identifier, this shared property stores selected question values." => "Umoliwia czenie pyta, ktre daj wyniki w rnych formatach. Gdy takie pytania s poczone ze sob za pomoc identyfikatora sprzenia, ta waciwo wspuytkowana przechowuje wybrane wartoci pyta."
// pehelp.searchEnabled: "Select if you want to update the drop-down menu contents to match the search query that a user is typing in the input field." => "Wybierz, czy chcesz zaktualizowa zawarto menu rozwijanego, aby pasowaa do zapytania wyszukiwania wpisywanego przez uytkownika w polu wejciowym."
// pehelp.valueTrue: "A value to save in survey results when respondents give a positive answer." => "Warto, ktr naley zapisa w wynikach ankiety, gdy respondenci udziel pozytywnej odpowiedzi."
// pehelp.valueFalse: "A value to save in survey results when respondents give a negative answer." => "Warto, ktra ma zosta zapisana w wynikach ankiety, gdy respondenci udziel odpowiedzi negatywnej."
// pehelp.showPreview: "It's not recommended to disable this option as it overrides the Preview image and makes it hard for a user to understand whether the files have been uploaded." => "Nie zaleca si wyczania tej opcji, poniewa zastpuje ona obraz podgldu i utrudnia uytkownikowi zrozumienie, czy pliki zostay przekazane."
// pehelp.needConfirmRemoveFile: "Triggers a prompt asking to confirm the file deletion." => "Uruchamia monit z prob o potwierdzenie usunicia pliku."
// pehelp.selectToRankEnabled: "Enable to rank only selected choices. Users will drag selected items from the choice list to order them within the ranking area." => "Wcz, aby uszeregowa tylko wybrane wybory. Uytkownicy bd przeciga wybrane elementy z listy wyboru, aby uporzdkowa je w obszarze rankingu."
// pehelp.dataList: "Enter a list of choices that will be suggested to the respondent during input." => "Wprowad list opcji, ktre zostan zasugerowane respondentowi podczas wprowadzania danych."
// pehelp.itemSize: "The setting only resizes the input fields and doesn't affect the width of the question box." => "To ustawienie zmienia tylko rozmiar pl wejciowych i nie wpywa na szeroko pola pytania."
// pehelp.itemTitleWidth: "Sets consistent width for all item labels in pixels" => "Ustawia sta szeroko dla wszystkich etykiet elementw w pikselach"
// pehelp.contentMode: "The \"Auto\" option automatically determines the suitable mode for display - Image, Video, or YouTube - based on the source URL provided." => "Opcja \"Auto\" automatycznie okrela odpowiedni tryb wywietlania - Obraz, Wideo lub YouTube - na podstawie podanego rdowego adresu URL."
// pehelp.altText: "Serves as a substitute when the image cannot be displayed on a user's device and for accessibility purposes." => "Suy jako substytut, gdy obraz nie moe by wywietlany na urzdzeniu uytkownika oraz w celu uatwienia dostpu."
// pehelp.rateColorMode: "Defines the color of the selected emoji when the Rating icon type is set to \"Smileys\". Choose between: \"Default\" - the selected emoji appears in default survey color; \"Scale\" - the selected emoji inherits color from the rating scale." => "Okrela kolor wybranego emoji, gdy typ ikony oceny jest ustawiony na \"Emotikony\". Wybierz pomidzy: \"Domylny\" - wybrany emoji pojawia si w domylnym kolorze ankiety; \"Skala\" - wybrany emoji dziedziczy kolor ze skali ocen."
// expression.name: "An expression ID that is not visible to respondents." => "Identyfikator wyraenia, ktry nie jest widoczny dla respondentw."
// expression.description: "Type an expression subtitle." => "Wpisz podtytu wyraenia."
// expression.expression: "An expression can include basic calculations (`{q1_id} + {q2_id}`), conditions (`{age} > 60`), and functions ('iif()`, `today()`, `age()`, `min()`, `max()`, `avg()`, etc.)." => "Wyraenie moe zawiera podstawowe obliczenia ('{q1_id} + {q2_id}'), warunki ('{wiek} > 60') i funkcje ('iif()', 'today()', 'age()', 'min()', 'max()', 'avg()' itp.)."
// pehelp.storeOthersAsComment: "Select to store the \"Other\" option value as a separate property in survey results." => "Wybierz, aby zapisa warto opcji \"Inne\" jako oddzieln waciwo w wynikach ankiety."
// p.swapOrder: "Swap the order of Yes and No" => "Zamie kolejno Tak i Nie"
