// datetimemask.pattern: "The pattern can contain separator characters and the following placeholders: `m` - for month number; `mm` - for month number, with leading zero for single-digit values; `d` - for day of the month; `dd` - for day of the month, with leading zero for single-digit values; `yy` - for the last two digits of the year; `yyyy` - for a four-digit year." => "Wzorzec moe zawiera znaki separatora i nastpujce symbole zastpcze: \"m\" - dla numeru miesica; 'mm' - dla numeru miesica, z zerem wiodcym dla wartoci jednocyfrowych; 'd' - dzie miesica; 'dd' - dla dnia miesica, z zerem wiodcym dla wartoci jednocyfrowych; 'yy' - dla dwch ostatnich cyfr roku; \"yyyy\" - dla roku czterocyfrowego."
