// imagepicker.contentMode: "Choose between \"Image\" and \"Video\" to set the content mode of the media selector. If \"Image\" is selected, ensure that all options provided are image files in the following formats: JPEG, GIF, PNG, APNG, SVG, BMP, ICO. Similarly, if \"Video\" is selected, ensure that all options are direct links to video files in the following formats: MP4, MOV, WMV, FLV, AVI, MKV. Please note that YouTube links are not supported for video options." => "Wybierz pomidzy \"Obraz\" i \"Wideo\", aby ustawi tryb zawartoci selektora multimediw. W przypadku wybrania opcji \"Obraz\" upewnij si, e wszystkie dostpne opcje s plikami graficznymi w nastpujcych formatach: JPEG, GIF, PNG, APNG, SVG, BMP, ICO. Podobnie, jeli wybrana jest opcja \"Wideo\", upewnij si, e wszystkie opcje s bezporednimi linkami do plikw wideo w nastpujcych formatach: MP4, MOV, WMV, FLV, AVI, MKV. Naley pamita, e linki do YouTube nie s obsugiwane w przypadku opcji wideo."
